* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch; }

html {
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

/* Let's get this party started */
::-webkit-scrollbar {
  width: 8px; }

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px; }

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #000;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1); }
  ::-webkit-scrollbar-thumb:window-inactive {
    background-color: #000; }

body.body {
  font-family: 'Montserrat';
  font-weight: 400;
  min-height: 100%;
  display: flex;
  line-height: 1.5;
  font-size: 16px;
  overflow: hidden auto; }
  body.body a {
    transition: all 0.3s linear;
    text-decoration: none; }
    body.body a:hover, body.body a:focus {
      outline: none !important;
      box-shadow: none;
      text-decoration: none; }
  body.body h1,
  body.body h2 {
    font-family: 'Cinzel'; }
  body.body p {
    line-height: 26px;
    letter-spacing: 0.44px; }
  body.body h2,
  body.body .h2 {
    font-size: 33.18px;
    font-family: 'Cinzel'; }
  body.body .h3,
  body.body h3 {
    font-family: 'Cinzel';
    font-size: 23.18px; }
  body.body .h4,
  body.body h4 {
    font-size: 18px;
    font-family: 'Cinzel'; }
  body.body .btn {
    border-radius: 0; }

main {
  position: relative;
  flex: 1 0 auto; }

.page-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  min-height: 100vh;
  width: 100%;
  overflow: hidden; }

.main {
  z-index: 2;
  position: relative; }

